      //   };
      // })('modal-open');

      $(function () {
        /*banner轮播图*/
        var bannerSwiper = new Swiper('#swiper-banner', {
          direction: 'horizontal',
          autoplay: {
            delay: 5000,
            disableOnInteraction: false,
          },//可选选项，自动滑动
          loop: true, // 循环模式选项
          centeredSlides: true,
          // 如果需要分页器
          pagination: {
            el: '#swiper-banner .banner-pagination',
          },
        });
        var newsSwiper = new Swiper('.news-wrap', {
          direction: 'horizontal',
          autoplay: {
            delay: 5000,
            disableOnInteraction: false,
          },
          loop: true, // 循环模式选项
          centeredSlides: true,
          slidesPerView: 1.4,
          initialSlide: 1,
          // spaceBetween : 15,
          // 如果需要分页器
          pagination: {
            el: '.news-wrap .banner-pagination',
            clickable: true,
          },
        });
        var video = document.getElementById("video");
        $('.video-img').click(function () {
          $(this).hide();
          $('#video').show();
          video.play();
        });
        if ("4" == 0) {
          $(".news").hide()
        }
      });

      document.getElementById("curYear").innerText = (new Date()).getFullYear();
      $(".menu-nav-wrap").on('click', function () {
        if ($(this).parent().hasClass('active')) {
          $(this).parent().removeClass('active')
        } else {
          $(this).parent().addClass('active').siblings().removeClass('active')
        }
      });

      $(".back-top-btn").click(function () {
        var scrollToptimer = setInterval(function () {
          var top = document.body.scrollTop || document.documentElement.scrollTop;
          var speed = top / 4;
          if (document.body.scrollTop != 0) {
            document.body.scrollTop -= speed;
          } else {
            document.documentElement.scrollTop -= speed;
          }
          if (top == 0) {
            clearInterval(scrollToptimer);
          }
        }, 50);
      });
